import React, { Component } from 'react';
import { Media,Card, CardImg, CardImgOverlay, CardText, CardBody, CardTitle,Breadcrumb, BreadcrumbItem } from 'reactstrap';
import DishDetail from './DishdetailComponent';
import {Link} from 'react-router-dom';
import { Loading } from './LoadingComponent';

function RenderMenuItem ({dish, onClick}) {
        return (
            <Card>
                <Link to={`/menu/${dish.id}`} >
                    <CardImg width="100%" src={dish.image} alt={dish.name} />
                    <CardImgOverlay>
                        <CardTitle className="head">{dish.name}</CardTitle>
                    </CardImgOverlay>
                </Link>
            </Card>
        );
    }

const Menu = (props)=>{
  const menu=props.dishes.dishes.map((dish)=>{
    return (
       <div className="col-12 col-md-5 m-1" key={dish.id}>
          <RenderMenuItem dish={dish} onClick={props.onClick}/>
       </div>
    );
  });

if (props.dishes.isLoading) {
            return(
                <div className="container">
                    <div className="row">            
                        <Loading />
                    </div>
                </div>
            );
        }
        else if (props.dishes.errMess) {
            return(
                <div className="container">
                    <div className="row"> 
                        <div className="col-12">
                            <h4>{props.dishes.errMess}</h4>
                        </div>
                    </div>
                </div>
            );
        }
        else
        return (
             <div className="container">
                 <div className="row">
                     <Breadcrumb >
                         <BreadcrumbItem className=" crumb"><Link to="/home">Home</Link></BreadcrumbItem>
                         <BreadcrumbItem active className=" crumb">Menu</BreadcrumbItem>
                     </Breadcrumb>
                     <div className="col-12">
                         <h3>Menu</h3>
                         <hr />
                     </div>                
                 </div>
                 <div className="row">
                     {menu}
                 </div>
             </div>
         );
};

export default Menu;